.header{
    background: var(--background);
}
.header_container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 16px 0;
    height: 88px;
    width: 1280px;
    position: relative;
    margin: auto;
}

.header_items {
    display: inline-flex;
    margin-left: -4px;
    margin-right: -4px;
}

.header_items_item {
    padding-left: 4px;
    padding-right: 4px;
    text-decoration: none;
}

.header_logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 24px;
    margin: auto;
    height: 40px;
    line-height: 0;
}

.header_logo svg {
    height: 100%;
}